import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Ltext, getLanguage } from '../utils/smsLog-translations';
import Pagination from '../components/Pagination';
import Modal from '../components/Modal';
import styles from '../assets/scss/smsLog.module.scss';
import '../assets/scss/smsLog.scss';

function SmsLog({ page_id, apiUrl, homeUrl, user_token, owner_id }) {
  const [smsLogs, setSmsLogs] = useState([]);
  const [loading, setLoading] = useState(true);
  const [tableLoading, setTableLoading] = useState(false);
  const [error, setError] = useState(null);
  const [searchQuery, setSearchQuery] = useState('');
  
  // Pagination state
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [totalItems, setTotalItems] = useState(0);
  const [itemsPerPage] = useState(20);

  // Popup state
  const [showPopup, setShowPopup] = useState(false);
  const [selectedMessage, setSelectedMessage] = useState('');

  useEffect(() => {
    fetchSmsLogs();
  }, [currentPage, searchQuery]);

  const fetchSmsLogs = async () => {
    try {
      // Use tableLoading for searches, regular loading for initial load
      if (searchQuery) {
        setTableLoading(true);
      } else {
        setLoading(true);
      }
      
      const response = await axios.get(`${apiUrl}`, {
        params: {
          action: 'getSmsLogs',
          page: currentPage,
          per_page: itemsPerPage,
          search: searchQuery || "",
          owner_id: owner_id
        },
        headers: {
          'Authorization': `Bearer ${user_token}`
        }
      });
      if (response.data.success) {
        setSmsLogs(response.data.data.sms_logs || []);
        setTotalItems(response.data.data.total || 0);
        setTotalPages(Math.ceil((response.data.data.total || 0) / itemsPerPage));
      } else {
        setError(Ltext(response.data.message) || Ltext("Failed to load SMS logs"));
      }
    } catch (err) {
      setError(Ltext("Error loading SMS logs"));
    } finally {
      if (searchQuery) {
        setTableLoading(false);
      } else {
        setLoading(false);
      }
    }
  };

  const handleSearch = (e) => {
    setSearchQuery(e.target.value);
    setCurrentPage(1); // Reset to first page when searching
  };

  const handlePageChange = (page) => {
    setCurrentPage(page);
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'sent':
        return styles.statusCompleted;
      case 'failed':
        return styles.statusFailed;
      case 'pending':
        return styles.statusPending;
        case 'delivered':
        return styles.statusCompleted;
      default:
        return styles.statusDefault;
    }
  };

  const handleShowMore = (message) => {
   // console.log('handleShowMore called with:', message);
    setSelectedMessage(message);
    setShowPopup(true);
   // console.log('showPopup set to true');
  };

  const closePopup = () => {
    //console.log('closePopup called');
    setShowPopup(false);
    setSelectedMessage('');
  };

  // Calculate SMS price based on character count
  const calculateSmsPrice = (content) => {
    if (!content || content.trim() === '') return 1;  // Empty content = 1 SMS = 1 kr
    
    const charCount = Math.ceil(content.length / 160);

    return charCount;


    // if (charCount <= 160) return 1;      // 1 SMS = 1 kr
    // if (charCount <= 320) return 2;      // 2 SMS = 2 kr
    // return 3;                            // 3+ SMS = 3 kr
  };

 // console.log('Current state - showPopup:', showPopup, 'selectedMessage:', selectedMessage);

  if (loading) {
    return (
      <div className={styles.smsLogWrapper}>
        <div className={styles.smsLogContainer}>
          <div className={styles.loading}>
            <div className={styles.spinner}></div>
            <p>{Ltext("Loading SMS logs...")}</p>
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className={styles.smsLogWrapper}>
        <div className={styles.smsLogContainer}>
          <div className={styles.error}>
            <h2>{Ltext("Error")}</h2>
            <p>{error}</p>
            <button 
              className={styles.btn} 
              onClick={fetchSmsLogs}
            >
              {Ltext("Retry")}
            </button>
          </div>
        </div>
      </div>  
    );
  }

  return (
    <div className={`smsLogWrapper ${styles.smsLogWrapper}`}>
      <div className={styles.smsLogContainer} style={{maxWidth: '100%', overflowX: 'auto'}}>
        {/* SMS Logs Section */}
        <div className={styles.smsLogSection}>
          <div className={styles.smsLogHeader}>
            <h2 className={styles.smsLogTitle}>{Ltext("SMS Logs")}</h2>
            <div className={styles.smsLogInfo}>
              <div className={styles.searchContainer}>
                <input
                  type="text"
                  placeholder={Ltext("Search (phone, message)...")}
                  value={searchQuery}
                  onChange={handleSearch}
                  className={styles.searchInput}
                />
              </div>
            </div>
          </div>

          <div className={styles.tableWrapper} style={{overflowX: 'auto', maxWidth: '100%'}}>
            {tableLoading ? (
              <div className={styles.tableLoading}>
                <div className={styles.spinnerSmall}></div>
                <p>{Ltext("Searching SMS logs...")}</p>
              </div>
            ) : (
              <table className={styles.smsLogTable} style={{minWidth: '800px'}}>
                <thead>
                  <tr>
                    <th>{Ltext("Date")}</th>
                    <th>{Ltext("Time")}</th>
                    <th>{Ltext("Phone Number")}</th>
                    <th>{Ltext("SMS Content")}</th>
                    <th>{Ltext("SMS Cost (kr)")}</th>
                    <th>{Ltext("Status")}</th>
                  </tr>
                </thead>
                
                <tbody>
                  {smsLogs.map((log) => {
                    // Parse the created_at datetime to separate date and time
                    const updatedDate = log.updated_at ? new Date(log.updated_at) : null;
                    const dateStr = updatedDate ? updatedDate.toLocaleDateString([], {
                      day: '2-digit',
                      month: '2-digit',
                      year: 'numeric'
                    }) : '-';
                    const timeStr = updatedDate ? updatedDate.toLocaleTimeString([], {hour: '2-digit', minute:'2-digit'}) : '-';
                    
                    // Format phone number with country code
                    const phoneNumber = log.country_code && log.phone_number ? 
                      `+${log.country_code} ${log.phone_number}` : 
                      (log.phone_number || '-');

                    // Calculate SMS cost based on content length
                    const smsCost = calculateSmsPrice(log.sms_content);
                    
                    return (
                      <tr key={log.id}>
                        <td>{dateStr}</td>
                        <td>{timeStr}</td>
                        <td>{phoneNumber}</td>
                        <td className={styles.messageCell}>
                          <div className={styles.messageText}>
                            {log.sms_content && log.sms_content.length > 100 ? 
                              `${log.sms_content.substring(0, 100)}...` : 
                              (log.sms_content || '-')
                            }
                          </div>
                          {log.sms_content && log.sms_content.length > 100 && (
                            <button 
                              className={styles.showMoreBtn}
                              onClick={() => handleShowMore(log.sms_content)}
                            >
                              {Ltext("Show More")}
                            </button>
                          )}
                        </td>
                        <td className={styles.priceCell}>
                          <span className={styles.priceBadge}>
                            {smsCost} kr
                          </span>
                          <small className={styles.smsCount}>
                            ({smsCost} SMS)
                          </small>
                        </td>
                        <td>
                          <span className={`${styles.statusBadge} ${getStatusColor(log.sms_status && log.sms_status.includes('accepted_at') ? 'sent' : 'failed')}`}>
                            {log.sms_status && log.sms_status.includes('accepted_at') ? Ltext('Sent') : Ltext('Not Delivered')}
                          </span>
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            )}
          </div>

          {/* Pagination Component */}
          <Pagination
            currentPage={currentPage}
            totalPages={totalPages}
            onPageChange={handlePageChange}
            totalItems={totalItems}
            itemsPerPage={itemsPerPage}
            showInfo={true}
          />
        </div>
      </div>

      {/* Message Popup */}
      {showPopup && (
          <Modal
            isOpen={showPopup}
            onClose={closePopup}
            title={Ltext("SMS Message")}
          >
            <div className={styles.popupBody}>
              <div className={styles.messageInfo}>
                <div className={styles.messageContent}>
                  <h4>{Ltext("Message Content:")}</h4>
                  <p>{selectedMessage}</p>
                </div>
                
                <div className={styles.messageDetails}>
                  <div className={styles.detailRow}>
                    <span className={styles.detailLabel}>{Ltext("Character Count:")}</span>
                    <span className={styles.detailValue}>{selectedMessage.length}</span>
                  </div>
                  <div className={styles.detailRow}>
                    <span className={styles.detailLabel}>{Ltext("SMS Count:")}</span>
                    <span className={styles.detailValue}>{calculateSmsPrice(selectedMessage)}</span>
                  </div>
                  <div className={styles.detailRow}>
                    <span className={styles.detailLabel}>{Ltext("Total Cost:")}</span>
                    <span className={styles.detailValue}>{calculateSmsPrice(selectedMessage)} kr</span>
                  </div>
                </div>
              </div>
            </div>
          </Modal>
      )}
    </div>
  );
}

export default SmsLog;
